@font-face {
    font-family: 'Dm Sans';
    font-weight: 400;
    font-style: normal;
    src: local("dm-sans-regular"), url(../fonts/dm_sans_400.woff2) format("woff2"), url(../fonts/dm_sans_400.woff) format("woff");
}

@font-face {
    font-family: 'Dm Sans SemiBold';
    font-weight: 600;
    font-style: normal;
    src: local("dm-sans-bold"), url(../fonts/dm-sans-600.woff2) format("woff2"), url(../fonts/dm-sans-600.woff) format("woff");
}

@font-face {
    font-family: 'Dm Sans Bold';
    font-weight: 700;
    font-style: normal;
    src: local("dm-sans-bold"), url(../fonts/dm_sans_bold) format("woff2"), url(../fonts/dm_sans_bold.woff) format("woff");
}

@font-face {
    font-family: 'Dm Serif Display';
    font-weight: 400;
    font-style: normal;
    src: local("dm-serif-display-400"), url(../fonts/dm-serif-dispaly-400.woff2) format("woff2"), url(../fonts/dm-serif-display-400.woff) format("woff");
}

@font-face {
    font-family: 'Dm Sans Italic';
    font-weight: 400;
    font-style: normal;
    src: local("dm_sans_400_italic"), url(../fonts/dm_sans_400_italic.woff2) format("woff2"), url(../fonts/dm_sans_400_italic.woff) format("woff");
}

body {
    font-family: 'Dm Sans';
    background-color: rgba(67, 76, 204, 0.05);
}

div.container {
    padding-right: 14px;
    padding-left: 14px;
}

@media (min-width: 1114px) {
    div.container {
        padding: 0;
        max-width: 1114px;
        margin: auto;
    }
}

:root {
    --header-padd-vertical: 16px;
}

body>header {
    background-color: #434CCC;
    padding-bottom: var(--header-padd-vertical);
    padding-top: var(--header-padd-vertical);
}

header h1 {
    font-family: "Dm Serif Display";
    font-size: 24px;
    line-height: 32px;
    font-weight: normal;
    text-transform: uppercase;
    margin: 0;
}

header h1 a {
    color: #ffffff;
    text-decoration: none;
}

body>header>div {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.icon_menu img {
    width: 20px;
    height: 16px;
}

@media (min-width: 1114px) {
    header h1 {
        font-size: 40px;
        line-height: 48px;
    }
}

body>header {
    position: relative;
}

header nav {
    padding: 1rem;
    background-color: gray;
    position: absolute;
    border-radius: .5rem;
    top: 0;
    left: 0;
    bottom: 0;
    width: 150px;
    height: 500px;
    box-shadow: 0 0 50px rgba(0, 0, 0, 0.5);
    transform: translateX(-120%);
    transition: transform 0.3s;
}

body>[type="checkbox"] {
    display: none;
}

body>[type="checkbox"]:checked~header nav {
    display: block;
    content: '';
    left: 0;
    top: 0;
    transform: translateX(-10%);
    transition: transform 0.3s;
}

.navigation_container {
    display: flex;
    flex-direction: column;
    margin: 0;
}

.navigation_container li {
    padding-block-start: 20px;
    padding-inline-start: 20px;
}

.navigation_container a {
    color: #ffffff;
    font-size: 16px;
    line-height: 24px;
}

.navigation_container a:hover {
    color: #000000;
    font-family: 'Dm Sans Bold';
    transition: color 1s;
}

@media (min-width: 1114px) {
    header nav {
        padding: 0;
        background: none;
        box-shadow: none;
        transform: none;
        display: block;
        position: relative;
        box-shadow: none;
        width: auto;
        height: auto;
        transition: none;
    }
    header label {
        display: none;
    }
    .navigation_container {
        flex-direction: row;
    }
    .navigation_container li {
        margin-inline-start: 26px;
        padding: 0;
    }
}

.button_container p {
    padding-top: 31px;
    padding-bottom: 61px;
}

.button_container p a {
    font-size: 16px;
    line-height: 24px;
    color: rgba(0, 0, 0, 0.5);
}

.button_container p a:hover {
    color: #000000;
    font-family: 'Dm Sans Bold';
    transition: color 1s;
}

ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

a {
    text-decoration: none;
}

h2 {
    font-family: "Dm Serif Display";
    color: #000000;
    font-size: 40px;
    line-height: 48px;
    font-weight: normal;
    margin: 0;
}

.article_text {
    font-size: 18px;
    line-height: 32px;
    color: #000000;
}

@media (min-width: 1114px) {
    h2 {
        font-size: 52px;
        line-height: 64px;
    }
}

.beauty_section_pictures .big_carve {
    width: 100%;
}

.beauty_section_pictures img {
    border-radius: 0.5rem;
}

.image_container {
    display: flex;
    flex-direction: row;
    overflow: hidden;
}

.image_container li:not(:nth-of-type(1)) {
    margin-inline-start: 9px;
}

.image_container li img {
    width: 137px;
}

@media (min-width: 1114px) {
    .image_container li img {
        opacity: 0.5;
    }
}

.photo_details {
    padding-top: 92px;
    padding-bottom: 75px;
}

.detail_list_container {
    display: grid;
    row-gap: 40px;
    column-gap: 106px;
}

.detail_list_container li:nth-of-type(1) {
    grid-row-start: 1;
}

.detail_list_container li:nth-of-type(2) {
    grid-column-start: 2;
}

.detail_list_container small,
.detail_list_container p {
    font-size: 16px;
    line-height: 24px;
}

.detail_list_container small {
    color: rgba(0, 0, 0, 0.5);
}

.detail_list_container p {
    color: #000000;
    font-family: 'Dm Sans Bold';
}

.detail_list_container b {
    font-family: 'Dm Sans Italic';
}

@media (min-width: 1114px) {
    .photo_details {
        padding-top: 89px;
        padding-bottom: 206;
    }
}

:root {
    --article-text-verticle-padd: 31px;
    --beauty-text-verticle-padd: 61px;
}

.information_section {
    padding-bottom: 106px;
}

.carve-information {
    font-size: 18px;
    line-height: 32px;
    color: #000000;
}

.beauty_section_text p {
    margin: 0;
    padding-top: 65px;
    padding-bottom: 82px;
}

.experience_information {
    font-size: 16px;
    line-height: 24px;
    color: rgba(0, 0, 0, 0.5);
    mix-blend-mode: normal;
    padding-top: var(--article-text-verticle-padd);
    padding-bottom: var(--article-text-verticle-padd);
}

.experience_information b {
    font-family: 'Dm Sans Italic';
}

@media (min-width: 1114px) {
    .article_information {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }
    .carve_details {
        padding-top: 87px;
        padding-bottom: 76px;
    }
    .beauty_section_text h2 {
        max-width: 457px;
    }
    .beauty_section_text p {
        max-width: 585px;
        margin: 0;
        padding-top: var(--beauty-text-verticle-padd);
        padding-bottom: var(--beauty-text-verticle-padd);
    }
}

.icon_list_container {
    display: flex;
    flex-direction: row;
}

.icon_list_container span {
    display: none;
}

.icon_list_container li:not(:first-child) {
    margin-left: 18px;
}

.icon_list_container li a svg {
    fill: rgba(0, 0, 0, 0.5);
    opacity: 0.3;
}

.icon_list_container li a svg:hover {
    fill: #000000;
    opacity: 1;
}

.section_subscribe {
    background-color: #FFDD78;
}

.section_subscribe>div {
    padding-top: 63px;
    padding-bottom: 54px;
}

.small_heading {
    font-size: 16px;
    line-height: 24px;
    letter-spacing: 0.3em;
    text-transform: uppercase;
    color: #000000;
}

div.subscribe {
    padding-top: 44px;
}

@media (min-width: 1114px) {
    .section_subscribe>div {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        column-gap: calc(218px - 16px);
        padding-top: calc(120px - 44px);
        padding-bottom: 104px;
    }
    .section_subscribe>div h2 {
        width: 382px;
    }
    div.subscribe ul {
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;
    }
    div.subscribe ul li:nth-of-type(2) {
        padding-top: 8px;
    }
}

:root {
    --btn-padding-vertical-start: 13px;
    --btn-padding-vertical-end: 11px;
}

.subscribe_button {
    background-color: #434CCC;
    text-align: center;
    border-radius: 0.5rem;
    width: 170px;
}

.subscribe_button a {
    font-size: 16px;
    line-height: 24px;
    color: #ffffff;
    padding-top: var(--btn-padding-vertical-start);
    padding-bottom: var(--btn-padding-vertical-end);
    display: block;
}

.subscribe_button:hover {
    background-color: #0000CD;
    transition: background-color 0.5s;
}

@media (min-width: 1114px) {
    .subscribe_button {
        margin-left: 54px;
        margin-bottom: 0;
    }
}

:root {
    --btn-padding-vertical-start: 13px;
    --btn-padding-vertical-end: 11px;
}

.email_input {
    display: flex;
    flex-direction: column;
}

.email_input label {
    font-family: 'Dm Sans Bold';
    font-size: 12px;
    line-height: 24px;
    color: #000000;
    text-transform: uppercase;
    padding-bottom: 12px;
}

.email_input input {
    background: #ffffff;
    border: 1px solid #DADADA;
    box-sizing: border-box;
    border-radius: 5px;
    padding-left: 16px;
    padding-top: var(--btn-padding-vertical-start);
    padding-bottom: var(--btn-padding-vertical-end);
}

.email_input ::placeholder {
    font-size: 16px;
    line-height: 24px;
    color: rgba(0, 0, 0, 0.5);
}

@media (min-width: 1114px) {
    .email_input input {
        padding-right: 172px;
    }
}

div.photograph_experience img {
    width: 70px;
    height: 48px;
    border-radius: 0.5rem;
}

.list_primary {
    display: flex;
    flex-direction: column;
}

.list_primary>li:nth-of-type(2) {
    padding-top: 26px;
}

ul.list_secondary {
    display: flex;
    flex-direction: row;
}

ul.list_secondary li:nth-of-type(2) p {
    margin: 0;
    padding-left: 30px;
}

.place {
    font-size: 16px;
    line-height: 24px;
    color: rgba(0, 0, 0, 0.5);
}

.date {
    font-family: 'Dm Sans Bold';
    font-size: 12px;
    line-height: 24px;
    color: #000000;
}

@media (min-width: 1114px) {
    .place {
        white-space: nowrap;
    }
}

:root {
    --padding-container-value: 78px;
    --padding-list-value: 7px;
}

div.photograph_more_links {
    padding-top: var(--padding-container-value);
    padding-bottom: var(--padding-container-value);
}

div.photograph_more_links ul li {
    padding-top: var(--padding-list-value);
}

div.photograph_more_links ul li a {
    font-size: 16px;
    line-height: 24px;
    color: rgba(0, 0, 0, 0.5);
}

div.photograph_more_links ul li a:hover {
    color: #000000;
    font-family: 'Dm Sans Bold';
    transition: color 1s;
}

div.photograph_more_links ul li:nth-of-type(1) {
    font-family: 'Dm Sans Bold';
    padding-bottom: calc(23px - var(--padding-list-value));
}

div.photograph_more_links ul li:nth-of-type(1) a {
    color: #000000;
}

@media (min-width: 1114px) {
    div.photograph_more_links {
        padding: 0;
    }
    div.photograph_more_links li:nth-of-type(1) {
        padding-top: 0;
    }
}

body>footer {
    background-color: #ffffff;
}

body>footer>div:nth-of-type(1) {
    padding-top: 75px;
    padding-bottom: 65px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.5);
}

body>footer>div:nth-of-type(2) {
    padding-bottom: 28px;
}

body>footer>div:nth-of-type(2) p {
    font-size: 16px;
    line-height: 24px;
    color: rgba(0, 0, 0, 0.5);
}

.photograph_introduction {
    font-size: 16px;
    line-height: 24px;
    color: rgba(0, 0, 0, 0.5);
    margin: 0;
    padding-bottom: calc(78px / 2);
}

.photograph_introduction h2 {
    font-family: "Dm Serif Display";
    font-size: 24px;
    line-height: 32px;
    font-weight: normal;
    text-transform: uppercase;
}

@media (min-width: 1114px) {
    body>footer>div:nth-of-type(1) {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        column-gap: 130px;
    }
    body>footer>div:nth-of-type(2) {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        padding-bottom: 75px;
    }
}